import React from "react";
import { NavLink } from "react-router-dom";
import web from "../src/images/ak.jpg";
const Navbar = () => {
    return (
        <>
            <div className="container-fluid  nav_bg">
                <div className="row">
                    <div className="col-10 mx-auto">



                        <nav className="navbar navbar-expand-lg navbar-light">

                            <div className="container-fluid">
                               <NavLink to="/About"><img src={web} className="logo" alt="Logo img" to="/About"/></NavLink>
                                <NavLink className="navbar-brand" to="/About">Akash</NavLink>


                                <button
                                    className="navbar-toggler"
                                    type="button"
                                    data-toggle="collapse"
                                    data-target="#navbarSupportedContent"
                                    aria-controls="navbarSupportedContent"
                                    aria-expanded="false"
                                    aria-label="Toggle navigation" >
                                    <i className="fas fa-bars"></i>
                                </button>


                                <div className="collapse navbar-collapse" id="navbarSupportedContent">

                                    <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                                        <li className="nav-item">
                                            <NavLink activeClassName='menu_active' exact className="nav-link active" aria-current="page" to="/">Home</NavLink>
                                        </li>
                                        <li className="nav-item">
                                            <NavLink activeClassName='menu_active' className="nav-link" to="/Services">Services</NavLink>
                                        </li>
                                        <li className="nav-item">
                                            <NavLink activeClassName='menu_active' className="nav-link" to="/About">About</NavLink>
                                        </li>
                                        <li className="nav-item">
                                            <NavLink activeClassName='menu_active' className="nav-link" to="/Contact">Contact</NavLink>
                                        </li>



                                    </ul>



                                </div>

                            </div>

                        </nav>
                    </div>
                </div>
            </div>

        </>
    );
};

export default Navbar;